.App {
    width: 100%; /* Ancho al 100% */
    padding-top: 50px; /* Separador de la parte superior */
    font-family: Lato, sans-serif; /* Tipo de letra (Lato se puede cambiar), sans-serif por si el navegador no reconoce Lato */
    display: flex; /* Utiliza un contenedor flexible */
    flex-wrap: wrap; /* Para que los datos no estén todos en la misma línea */
    justify-content: center; /* Centra el contenido */
    align-items: center; /* Alinea los elementos al centro */
    background-image: url("./imagenes/80.jpg"); /* Imagen de fondo */
    background-size: cover; /* La imagen cubre toda la pantalla */
    background-position: center; /* Centra la imagen */
    background-repeat: no-repeat; /* Evita que la imagen se repita */
}

.logo-contenedor {
    width: 20%; /* El contenedor del logo ocupa el 20% del ancho */
    height: 20px; /* La altura del contenedor del logo */
    margin-bottom: 40px; /* Margen inferior */
    display: flex; /* Se utiliza flexbox */
    align-items: center; /* Centra verticalmente el contenido */
    justify-content: center; /* Centra horizontalmente el contenido */
}

.logo {
    height: 100px; /* La altura del logo */
    max-width: 200px; /* El ancho máximo del logo */
    border-radius: 100px; /* Borde redondeado del logo */
}

.Contenedor-calculadora {
    width: 70%; /* El contenedor de la calculadora ocupa el 70% del ancho */
    max-width: 600px; /* El contenedor de la calculadora tiene un ancho máximo de 600px */
    height: 700px; /* La altura del contenedor de la calculadora */
    padding: 14px; /* Espaciado interno */
    border-radius: 50px; /* Bordes redondeados */
}

.fila {
    margin: 40px; /* Márgenes de 40px */
    display: flex; /* Usar flexbox para los elementos en una fila */
    align-items: center; /* Centra verticalmente los elementos */
    justify-content: center; /* Centra horizontalmente los elementos */
}

/* Reglas específicas para pantallas pequeñas */
@media (max-width: 768px) {
    .logo-contenedor {
        width: 50%; /* El contenedor del logo ocupará el 50% en pantallas pequeñas */
    }
    
    .logo {
        height: 80px; /* El logo será más pequeño */
        max-width: 150px; /* El logo tendrá un ancho máximo más pequeño */
    }
    
    .Contenedor-calculadora {
        width: 90%; /* El contenedor de la calculadora ocupará el 90% en pantallas pequeñas */
        height: auto; /* La altura se ajusta automáticamente */
    }
    
    .fila {
        margin: 20px; /* Se reduce el margen entre las filas */
    }
}
